import fetch from './fetch';

// ***  ***
export const login = data => fetch('/users/login', data, 'POST'); //  
export const logout = data => fetch('/user/logout', data, 'POST'); // 
export const uploadImg = data => fetch('/upload', data, 'POST', 'httprequest', true); // 
export const editAttachment = (data) => fetch('/attachment', data, 'POST'); // |
export const uploadToken = data => fetch('/uploadtoken', data); // token
export const deleteData = data => fetch('/setenable', data, 'POST'); // 

export const getSwiperCount = data => fetch('/carousels/count', data); // 
export const getSwiperList = data => fetch('/carousels', data); //
export const addSwiper = data => fetch('/carousels', data, 'POST'); //
export const updateSwiper = (id,data) => fetch('/carousels/' + id, data, 'POST'); //


