<!-- table  -->
<template>
	<div class="page-body">

		<!--  -->
  		<div class="page-header">
    		<h1 class="page-title"></h1>
    		<el-breadcrumb>
      		<el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      		<el-breadcrumb-item ></el-breadcrumb-item>
    		</el-breadcrumb>
  		</div>

  		<!--  -->
  		<div class="box">
  			<el-form ref="form" :model="form" :rules="rules" label-width="150px">
      		<el-form-item label="" prop="title">
        			<m-input block placeholder="" v-model="form.title"/>
      		</el-form-item>
      		<el-form-item label="" prop="desc">
            	<m-input placeholder="" type="textarea" v-model="form.desc" block/>
         	</el-form-item>
            <el-form-item label="" prop="value">
               <el-upload
                  class="avatar-uploader"
                  :headers="headers"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                  :on-progress="avatorProgress"
                  :before-upload="beforeAvatarUpload">
                  <img v-if="isImg" :src="imgUrl + form.value" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
               </el-upload>
            </el-form-item>
            <el-form-item label="" v-if="form.edit == 1">
               <el-upload
                  class="upload-demo"
                  ref="upload"
                  :on-remove="handleRemove"
                  :on-success="uploadSuccess"
                  :before-remove="beforeRemove"
                  multiple
                  :limit="5"
                  :on-exceed="handleExceed"
                  :file-list="fileList">
                  <el-button slot="trigger" size="small" type="primary"></el-button>
                  <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
               </el-upload>
            </el-form-item>
            <el-form-item label="" prop="html">
               <div class="edit_container">
                  <quill-editor v-model="form.html"
                     ref="myQuillEditor"
                     class="editer"
                     placeholder=""
                     :options="editorOption">
                  </quill-editor>
               </div>
            </el-form-item>
      		<el-form-item>
        			<el-button size="small" type="primary" @click="edit('form')" :loading="on_submit_loading"></el-button>
        			<el-button size="small" @click="$router.back()"></el-button>
      		</el-form-item>
    		</el-form>
  		</div>

  	</div>
</template>

<script>
import { imgUrl, uploadurl } from '@/api/env'
import { quillEditor, Quill } from 'vue-quill-editor'
import {container, ImageExtend, QuillWatch} from 'quill-image-extend-module'
Quill.register('modules/ImageExtend', ImageExtend)
import { addSwiper, updateSwiper, editAttachment } from '@/api/getData'
export default {
	components: { quillEditor },
	data() {
		return {
			imgUrl,
			uploadurl,
			headers: { token: localStorage.getItem('token')},
			form: this.$route.query.item,
			editorOption: {
            placeholder: '',
            modules: {
               ImageExtend: {
                  loading: true,
                  name: 'img',
                  action: uploadurl + '?token=' + localStorage.getItem('token'),
                  response: (res) => {

                     return imgUrl + res.data[0];
                  }
               },
               toolbar: {
                  container: container,
                  handlers: {
                     'image': function () {
                        QuillWatch.emit(this.quill.id)
                     }
                  }
               }
            }
         },
         rules: {
            title: [ { required: true, message: '', trigger: 'blur' } ],
            value: [ { required: true, message: '', trigger: 'blur' } ],
            desc: [ { required: true, message: '', trigger: 'blur' } ],
            html: [ { required: true, message: '', trigger: 'blur' } ]
         },
         isImg: false,
         fileList: [],
         on_submit_loading: false
		}
	},
	created() {

      //  token  upload  token
      var token = localStorage.getItem('token');
      if ( [undefined, 'undefined', null, 'null', ''].indexOf(token) == -1 ) {
         this.uploadurl += '?token=' + token;
      }

      // 
		if ( this.form.edit == 1 ) {
         this.isImg = true;
      }
	},
	methods: {

      // 
      edit(formName) {
         this.$refs[formName].validate((valid) => {
            if (valid) {

               // 
               var data = {
                  title: this.form.title,
                  value: this.form.value,
                  html: this.form.html
               }

               if ( this.form.edit == 1 ) {
                  this.update(data);                                
               } else {
                  this.add(data);             
               }
               this.on_submit_loading = true;
            } else {
               return false;
            }
         });
      },

      // 
      async add(data) {

         var ret = await addSwiper(data);
         if ( ret.code == 200 ) {
            this.$message.success('');
            this.$router.go(-1);
         } else {
            this.$message.error('');
            this.on_submit_loading = false;
            console.log('', ret);
         }

      },

      // 
      async update(data) {

         var ret = await updateSwiper(this.form._id,data);
         if ( ret.code == 200 ) {
            this.$message.success('');
            this.$router.go(-1);
         } else {
            this.$message.error('');
            this.on_submit_loading = false;
            console.log('', ret);
         }

      },

		// 
      avatorProgress(res, file) {
         this.$message('');
      },
      handleAvatarSuccess(res, file) {
         if( res.code == 200 ) {
            this.$message.success('');
            this.form.value = res.data[0];
            this.isImg = true;
         } else {
            this.$message.error('');
         }
      },
      beforeAvatarUpload(file) {
         const isRightType = (file.type === 'image/jpeg') || (file.type === 'image/png');
         const isLt2M = file.size / 1024 / 1024 < 2;

         if (!isRightType) {
            this.$message.error(' jepg/png !');
         }
         if (!isLt2M) {
            this.$message.error(' 2MB!');
         }
         return isRightType && isLt2M;
      },

      // 
      handleExceed(files, fileList) {
         console.log(files, fileList);
      },

      // 
      async uploadSuccess(res, file, filelist) {

         if ( res.code == 200 ) {

            this.$message.info('');

            // 
            var data = {
               model: 'carousel',
               type: 0,
               name: file.name,
               file: res.data[0],
               id: this.form._id
            }
            this.fileList.push(data);

            var ret = await editAttachment(data);
            if ( ret.code == 200 ) {
               this.$message.success('');
            } else {
               this.$message.error('');
               console.log('', ret);
            }

         } else {
            this.$message.error('');
            console.log('', res);
         }

      },

      // 
      beforeRemove(file, fileList) {
         return this.$confirm(` ${ file.name }`);
      },

      // 
      async handleRemove(file, filelist) {

         var data = {
            model: 'carousel', 
            type: 1, 
            id: this.form._id, 
            fileid: file.fileid
         }

         var ret = await editAttachment(data);
         if ( ret.code == 200 ) {
            this.$message.success('');
         } else {
            this.$message.error('');
            console.log('', ret);
         }
      },

	}
}
</script>

<style>
.box-body{
	width: 100%;
	overflow: auto;
}
</style>



